.loader {
    width: 55px;
    height: 55px;
    border: 5px solid #FFF;
    border-bottom-color: #6d62e0;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}